import * as firebase from 'firebase'

import { Try } from './../utils/utils'

// Firebase Config
const config = {
  apiKey: process.env.FIREBASE_API_KEY,
  authDomain: process.env.FIREBASE_AUTH_DOMAIN,
  databaseURL: process.env.FIREBASE_DB_URL,
  projectId: process.env.FIREBASE_PROJECT_ID,
  messagingSenderId: process.env.FIREBASE_MESSAGING_SENDER_ID
}

class FirebaseError {
  constructor (error) {
    this.code = Try(error).code
    this.message = Try(error).message
  }
}

class Firebase {
  constructor (config) {
    firebase.initializeApp(config)
  }

  async getGuests () {
    return firebase.database()
      .ref('guests').once('value')
      .catch(err => new FirebaseError(err))
      .then(guests => guests.val().map((guest, index) => {
        guest.id = index
        return guest
      }))
  }

  async updateGuestStatus (id, status) {
    return firebase.database().ref('guests/' + id).update({
      status: status
    })
  }
}

export default new Firebase(config)



