<template>
  <div>
    <input 
      v-model.trim="query"
      :placeholder="placeholder" 
      @input="onInput($event.target.value)"
      @blur="isOpen = false"
      @keyup.esc="isOpen = false"
      @keydown.down="moveDown"
      @keydown.up="moveUp"
      @keydown.enter="onSelect"
    >
    <ul v-show="isOpen">
      <li
        v-for="(suggestion, index) in suggestions" 
        :key="index"
        :class="{
          'highlighted': index === positionSelected
        }"
        @mouseenter="positionSelected = index"
        @mousedown="onSelect"
      >
        <slot name="suggestion" :suggestion="suggestion" />
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data: () => ({
    isOpen: false,
    positionSelected: 0,
    query: '',
    suggestions: []
  }),
  props: {
    placeholder: String,
    filter: {
      type: Function,
      required: true
    },
    updateInputValueOnSelect: Function
  },
  methods: {
    onInput (value) {
      this.suggestions = this.filter(value)
      this.isOpen = !!value && this.suggestions.length > 0
    },
    moveDown () {
      if (!this.isOpen) return

      this.positionSelected = (this.positionSelected + 1) % this.suggestions.length
    },
    moveUp () {
      if (!this.isOpen) return

      this.positionSelected = this.positionSelected - 1 < 0 ? this.suggestions.length - 1 : this.positionSelected - 1
    },
    onSelect () {
      let selected = this.suggestions[this.positionSelected]
      this.$emit('onSelect', selected)
      this.isOpen = false
      this.query = this.updateInputValueOnSelect(selected)
    }
  }
}
</script>



