<template>
  <!-- Presence -->
  <div class="spring presence">
    <h1>Presence RSVP</h1>
    <div class="divider"></div>
    <p></p>
    <div class="form">
      <label class="form__row">
        <span class="form__label"></span>
        <input type="text" class="form__input">
        <!-- <AutoComplete 
          placeholder="Primeiro e último nome"
          :options="guests"
          :filter="filterGuests"
          :updateInputValueOnSelect="onGuestSelectedUpdateInputValue"
          @onSelect="onGuestSelected"
          >
            <p slot="suggestion" slot-scope="props">
            {{ props.suggestion.name }} || {{props.suggestion.status}}
          </p>
        </AutoComplete> -->
      </label>
      <label class="form__row">
        <span class="form__label"></span>
        <input type="text" class="form__input">
      </label>
      <label class="form__row">
        <span class="form__label"></span>
        <input type="text" class="form__input">
      </label>
      <div class="form__buttons">
        <button></button>
        <button></button>
      </div>
    </div>
  </div>
</template>

<script>
  import firebase from './../db/firebase'
  import { accentFolding } from './../utils/utils'

  import AutoComplete from './ui/AutoComplete'

  export default {
    data: () => ({
      guests: []
    }),
    components: {
      AutoComplete
    },
    methods: {
      async loadGuests () {
        this.guests = await firebase.getGuests()
      },
      filterGuests (query) {
        let q = accentFolding(query.toLowerCase())
        return this.guests.filter(g => {
          let name = accentFolding(g.name.toLowerCase())
          return name.includes(q)
        })
      },
      onGuestSelectedUpdateInputValue (guest) {
        return guest.name
      },
      onGuestSelected (guest) {
        console.log('AQUI')
        console.log(guest)
      }
    },
    mounted () {
      this.loadGuests()
    }
  }
</script>


